<script>
import axios from "axios";
import { mapState } from "vuex";

export default {
  name: "HuiZong",
  data() {
    return {
      evaluations: null
    };
  },
  computed: {
    ...mapState({
      group1Data: (state) => state.category.group1,
      group2Data: (state) => state.category.group2,
      group3Data: (state) => state.category.group3,
    }),
    mergedData() {
        const keys = ["001", "002", "003", "004"]; // ÂÅáËÆæÊØè‰∏™ËØÑ‰ª∑Áª¥Â∫¶ÁöÑ ID ÊòØÂõ∫ÂÆöÁöÑ
        return keys.map((id) => {
          const group1 = this.group1Data.find((d) => d.id === id) || {};
          const group2 = this.group2Data.find((d) => d.id === id) || {};
          const group3 = this.group3Data.find((d) => d.id === id) || {};

          // ËÆ°ÁÆóÊØè‰∏™Â∞èÁªÑÁöÑ‰ªñËØÑÂπ≥ÂùáÂàÜ
          const group1OtherAverage =
            [group2.group1, group3.group1]
              .filter((score) => score !== undefined && score !== null) // ËøáÊª§Êó†ÊïàÊï∞ÊçÆ
              .reduce((sum, score) => sum + score, 0) /
            [group2.group1, group3.group1].filter((score) => score !== undefined && score !== null).length || "Êú™ËØÑ";

          const group2OtherAverage =
            [group1.group2, group3.group2]
              .filter((score) => score !== undefined && score !== null)
              .reduce((sum, score) => sum + score, 0) /
            [group1.group2, group3.group2].filter((score) => score !== undefined && score !== null).length || "Êú™ËØÑ";

          const group3OtherAverage =
            [group1.group3, group2.group3]
              .filter((score) => score !== undefined && score !== null)
              .reduce((sum, score) => sum + score, 0) /
            [group1.group3, group2.group3].filter((score) => score !== undefined && score !== null).length || "Êú™ËØÑ";

          return {
            id,
            name: group1.name || "Êú™Áü•Áª¥Â∫¶",
            group1,
            group2,
            group3,
            otherAverages: {
              group1: group1OtherAverage,
              group2: group2OtherAverage,
              group3: group3OtherAverage,
            },
          };
  });
    },


  },
  mounted() {
